import { Component, OnInit } from '@angular/core';
import { SessionService } from '@session';
import { getGuidComb, ToastService, WellKnownRoutes } from '@core';
import { IframeService } from '@core/services/iframe.service';
import { LoggerService } from '@core/services/logger.service';
import {
  CompanyTitles,
  CompanyType,
  EnvelopeStatus,
  HouseholdFilers,
  HouseholdFilersStatus,
  User
} from '@model';
import { Observable } from 'rxjs';
import { first } from 'rxjs/operators';
import { DocumentsViewService } from '../shared/docs/documents-view.service';
import { DocumentsVm } from '../shared/docs/documents-vm';

@Component({
  selector: 'sp-documents',
  templateUrl: './documents.component.html'
})
export class DocumentsComponent implements OnInit {
  error: string;
  returnTo: string;
  returnToLabel = 'Admin Account View';
  showList = false;
  vms$: Observable<DocumentsVm[]>;
  hhFilers: HouseholdFilers;
  user: User;
  isAdmin: boolean;
  companyTitles: string[];
  // three exclusive choices
  isSingle: boolean;
  isJoint: boolean;
  isCompany: boolean;
  esignLink: string;

  constructor(
    private documentsViewService: DocumentsViewService,
    private iframeService: IframeService,
    private logger: LoggerService,
    private toastService: ToastService,
    private sessionService: SessionService
  ) {
    this.esignDone = this.esignDone.bind(this);
    this.esignError = this.esignError.bind(this);
  }

  ngOnInit(): void {
    window.scrollTo(0, 0);

    this.sessionService.currentUser$.pipe(first()).subscribe(u => (this.user = u));
    this.sessionService.currentCompany$.pipe(first()).subscribe(co => {
      this.companyTitles = CompanyTitles[co.companyType] || CompanyTitles[CompanyType.Corporation];
    });
    this.documentsViewService.isAdmin$.pipe(first()).subscribe(isAdmin => {
      this.returnTo = isAdmin ? WellKnownRoutes.AdminAccountView : null;
      this.isAdmin = isAdmin;
    });
    this.documentsViewService.hhFilers$.pipe(first()).subscribe(h => {
      this.hhFilers = h;
      this.isCompany = !h;
      this.isJoint = h && h.filingStatus === HouseholdFilersStatus.Joint;
      this.isSingle = h && h.filingStatus === HouseholdFilersStatus.Single;
    });
    this.vms$ = this.documentsViewService.vms$;
    this.reloadEsignStatus();
  }

  setRole(vm: DocumentsVm) {
    if (this.hhFilers && !vm.isSigner && vm.user) {
      if (this.hhFilers.primaryFirstName === vm.user.firstName) {
        vm.isSigner = 'primary';
        this.changeRole(vm);
      } else if (this.hhFilers.spouseFirstName === vm.user.firstName) {
        vm.isSigner = 'spouse';
        this.changeRole(vm);
      }
    }
    return true;
  }

  changeRole(vm: DocumentsVm) {
    // console.log(vm);
    if (this.isCompany) {
      vm.signerTitle = '';
      if (vm.isSigner === 'yes' && this.user) {
        vm.signerEmail = this.user.email;
        vm.signerFirstName = this.user.firstName;
        vm.signerLastName = this.user.lastName;
      } else {
        vm.signerEmail = '';
        vm.signerFirstName = '';
        vm.signerLastName = '';
      }
    } else {
      vm.signerFirstName = this.hhFilers.primaryFirstName;
      vm.signerLastName = this.hhFilers.primaryLastName;
      vm.spouseFirstName = this.hhFilers.spouseFirstName;
      vm.spouseLastName = this.hhFilers.spouseLastName;
      if (vm.isSigner === 'yes' || (vm.isSigner === 'primary' && this.user)) {
        vm.signerEmail = this.user.email;
        vm.spouseEmail = '';
      } else if (vm.isSigner === 'spouse' && this.user) {
        vm.spouseEmail = this.user.email;
        vm.signerEmail = '';
      }
    }
  }

  clearError() {
    this.error = '';
  }

  private esignDone(status: string) {
    this.error = '';
    if (status === 'sp_invalid_user') {
      this.esignError('Unable to get user information', `esignDone error status ${status}`);
    } else if (status === 'sp_no_docusign_info') {
      this.esignError(
        'Document Signing service cannot find the documents',
        `esignDone error status ${status}`
      );
    } else if (status !== 'signing_complete' && status !== 'viewing_complete') {
      this.esignWarning('Document signing not completed');
      this.reloadEsignStatusAfterDelay();
    } else {
      this.esignSuccess('Document signing completed');
      this.reloadEsignStatusAfterDelay();
    }
  }

  private esignError(message: string, applicationError?: string) {
    if (applicationError) {
      // error info for a programmer
      this.logger.error(`DocumentsContainerComponent: ${applicationError}`);
    }
    this.error = message;
    // TODO: Do not use Toast for errors.
    this.toastService.show(message, 'E-Sign', 'error');
  }

  private esignWarning(message: string) {
    this.clearError();
    this.toastService.show(message, 'E-Sign', 'warning');
  }

  private esignSuccess(message: string) {
    this.clearError();
    this.toastService.show(message, 'E-Sign', 'success');
  }

  private gotoEsign(eSignEnvelopeId: string) {
    this.clearError();
    this.documentsViewService.getEsignEnvelopeUrl(eSignEnvelopeId).subscribe(url => {
      if (url) {
        this.esignLink = url;
        // this.iframeService.show(url, this.esignDone);
      } else {
        this.esignError(
          'Unable to open document signing.  Please try again, or check your email for a DocuSign message.',
          `gotoEsign: No envelope URL for envelope ${eSignEnvelopeId}`
        );
      }
    });
  }

  showESignWindow() {
    this.iframeService.show(this.esignLink, this.esignDone)
  }

  reloadEsignStatus() {
    this.documentsViewService.loadDocumentPackage().subscribe();
  }

  reloadEsignStatusAfterDelay() {
    setTimeout(() => this.reloadEsignStatus(), 3000);
  }

  private makeSigners(vm: DocumentsVm) {
    const signers = vm.signers || [];
    // remove and replace all signers
    signers.forEach(s => (s.isRemoved = true));
    const userIsPrimary = vm.isSigner === 'yes' || vm.isSigner === 'primary';
    const userIsSpouse = vm.isSigner === 'spouse';
    signers.push({
      id: getGuidComb(),
      companyId: vm.envelope.companyId,
      envelopeId: vm.envelope.id,
      email: vm.signerEmail,
      eSignOrder: 1,
      firstName: vm.signerFirstName,
      isEmbedded: userIsPrimary,
      isRemoved: false,
      lastName: vm.signerLastName,
      role: 'signer',
      status: 'local',
      statusDate: new Date(),
      title: vm.signerTitle,
      userId: userIsPrimary ? vm.user.id : null
    });
    if (this.isJoint) {
      signers.push({
        id: getGuidComb(),
        companyId: vm.envelope.companyId,
        envelopeId: vm.envelope.id,
        email: vm.spouseEmail,
        eSignOrder: 1,
        firstName: vm.spouseFirstName,
        isEmbedded: userIsSpouse,
        isRemoved: false,
        lastName: vm.spouseLastName,
        role: 'signer',
        status: 'local',
        statusDate: new Date(),
        title: null,
        userId: userIsSpouse ? vm.user.id : null
      });
    }
    return signers;
  }

  sendEmail(vm: DocumentsVm) {
    if (!(vm && vm.envelope)) {
      this.reloadEsignStatus();
      return;
    }
    if (vm.isSent) {
      return;
    }
    this.clearError();
    const signers = this.makeSigners(vm);
    this.documentsViewService.sendEnvelope(vm.envelope.id, signers).subscribe(r => {
      if (r.status === 'sent') {
        vm.isSent = true;
        this.reloadEsignStatusAfterDelay();
      } else {
        this.esignError(
          'Unable to send documents to DocuSign.  Please try again.',
          `sendEmail error status: ${r.status}`
        );
      }
    });
  }

  showESign(vm: DocumentsVm) {
    this.clearError();
    const envelope = vm && vm.envelope;

    if (!envelope) {
      this.reloadEsignStatus();
    } else if (envelope.status === EnvelopeStatus.sent && envelope.eSignEnvelopeId) {
      this.gotoEsign(envelope.eSignEnvelopeId);
    } else {
      const signers = this.makeSigners(vm);
      this.documentsViewService.sendEnvelope(vm.envelope.id, signers).subscribe(r => {
        if (r.status === 'sent') {
          vm.isSent = true;
          if (vm.isSigner && vm.isSigner !== 'no') {
            this.gotoEsign(r.envelope.eSignEnvelopeId);
          }
        } else {
          this.esignError(
            'Unable to send documents to DocuSign.  Please try again.',
            `showESign error status: ${r.status}`
          );
        }
      });
    }
  }

  showView(vm: DocumentsVm) {
    if (vm && vm.envelope.eSignEnvelopeId && vm.isSigner) {
      this.gotoEsign(vm.envelope.eSignEnvelopeId);
    } else {
      this.reloadEsignStatus();
    }
  }

  gotoDashboard() {
    this.documentsViewService.gotoDashboard();
  }
}
